  ::-webkit-scrollbar {
    width: 12px;
  }
  
  ::-webkit-scrollbar-thumb {
    background: linear-gradient(transparent, #F6AA1C);
    border-radius: 6px;
  }
  
  .pattern{
    background-color: #404040;
    opacity: 1;
    background-image:  linear-gradient(30deg, #393434 12%, transparent 12.5%, transparent 87%, #393434 87.5%, #393434), linear-gradient(150deg, #393434 12%, transparent 12.5%, transparent 87%, #393434 87.5%, #393434), linear-gradient(30deg, #393434 12%, transparent 12.5%, transparent 87%, #393434 87.5%, #393434), linear-gradient(150deg, #393434 12%, transparent 12.5%, transparent 87%, #393434 87.5%, #393434), linear-gradient(60deg, #282424 25%, transparent 25.5%, transparent 75%, #282424 75%, #282424), linear-gradient(60deg, #282424 25%, transparent 25.5%, transparent 75%, #282424 75%, #282424);
    background-size: 20px 35px;
    background-position: 0 0, 0 0, 10px 18px, 10px 18px, 0 0, 10px 18px;
  }


  .sidebar{
    background: linear-gradient(180deg, rgba(57,52,52,1) 0%, rgba(57,52,52,1) 51%, rgba(0,212,255,0) 100%);
  }

  body{
    background-color: #282424;
